package io.github.dennistsar.sirs_kobweb.states

import Entry
import Instructor
import School
import Semester
import androidx.compose.runtime.*
import getAvesAndTotal
import getTotalRatings
import io.github.dennistsar.sirs_kobweb.misc.None
import io.github.dennistsar.sirs_kobweb.misc.jsFormatNum
import io.github.dennistsar.sirs_kobweb.misc.plusElementAtStart
import kotlinx.coroutines.CoroutineScope
import kotlinx.coroutines.launch
import remote.GithubSource
import substringAfterBefore

@Stable
class DataPageVM(
    private val repository: GithubSource,
    private val coroutineScope: CoroutineScope,
    urlParams: Map<String, String>,
) {
    var state: DataPageState by mutableStateOf(DataPageState())
        private set

    private lateinit var globalData: GlobalData
    private var deptData by mutableStateOf(DeptData())

    private var initialLoading by mutableStateOf(true)
    var pageLoading by mutableStateOf(false)

    val minSemState = MinSemesterState()

    private val entriesByProf by derivedStateOf {
        val semesterFilter = Semester(minSemState.value)
        deptData.allEntriesByProf.filter {
            it.value.last().semester >= semesterFilter // because entries should be sorted in ascending order
        }
    }

    val url: String get() = getUrl()

    val status
        get() = when {
            // schoolMap.isEmpty() can work for initialLoading but doesn't account for dept/prof loading
            initialLoading -> Status.InitialLoading
            !state.prof.selected.isBlankOrNone() -> Status.Prof
            !state.course.selected.isBlankOrNone() -> Status.Course
            else -> Status.Dept
        }

    val mapToDisplay: Map<String, List<String>>? by derivedStateOf {
        when (status) {
            Status.Dept -> entriesByProf.toDisplayMap()
            Status.Course -> entriesByProf.mapValues { (_, v) ->
                v.filter { it.course == state.course.selected }
            }.filterValues { it.isNotEmpty() }.toDisplayMap()

            else -> null
        }
    }

    val teachingInstructors: List<String>
        get() {
            val courseString = with(state) { "${school.selected}:${dept.selected}:${course.selected}" }
            return deptData.teachingMap[courseString]
                .takeIf { status == Status.Course }.orEmpty()
        }

    val pageTitle: String
        get() {
            return when (status) {
                Status.InitialLoading -> ""
                Status.Prof -> with(state) { "${prof.selected} (${getCode()})" }
                Status.Dept -> globalData.deptMap[state.dept.selected]?.let { "$it (${getCode()})" }
                    ?: throw IllegalStateException("Invalid dept (${state.dept.selected}")

                else -> getCode()
            }
        }

    inner class SearchBarVM {
        var searchBoxInput by mutableStateOf("")
        var searchBarClickedOnce by mutableStateOf(false)

        val searchBarSuggestions by derivedStateOf {
            // to not slow down initial page load, add suggestions to DataList after search bar is initially clicked
            if (!searchBarClickedOnce) emptyList()
            else deptList + profList
        }

        private val profList by derivedStateOf {
            globalData.allInstructors
                .filter { it.latestSem >= Semester(minSemState.value) }
                .map { "${it.name} (${it.school}:${it.dept})" }
        }

        private val deptList = globalData.schoolMap.flatMap { (code, school) ->
            school.depts.map { "$code:$it - ${globalData.deptMap[it]}" }
        }

        fun onEnterSearch() {
            val input = searchBoxInput
            searchBoxInput = ""
            // valid states: "SMITH, JOHN (01:198)", "01:198 - Computer Science ", "01:198", "01:198:112"
            val school = input.substringAfterBefore("(", ":")
            val dept = input.substringAfterBefore(":", ")")

            if (input.contains("(")) { // assuming no depts have "(" in them - dangerous/wrong?
                val prof = input.substringBefore(" (")
                selectSchool(school = school, dept = dept, prof = prof)
            } else {
                val course = dept.substringAfter(":", None)
                val newDept = dept.substringBefore(":").substringBefore(" ")
                    .also { if (globalData.schoolMap[school]?.depts?.contains(it) != true) return }
                selectSchool(school = school, dept = newDept, course = course)
            }
        }
    }

    lateinit var searchBarVM: SearchBarVM
        private set

    fun selectSchool(
        school: String?,
        dept: String? = null,
        course: String? = null,
        prof: String? = null,
    ) {
        (globalData.schoolMap[school] ?: globalData.schoolMap["01"])?.let {
            val newDept = dept.takeIf { i -> i in it.depts } ?: it.depts.first()
            selectDept(dept = newDept, school = it, course = course, prof = prof)
        } ?: throw IllegalStateException("01 School Not Found")
    }

    fun selectDept(
        dept: String,
        school: School = globalData.schoolMap[state.school.selected]
            ?: throw IllegalStateException("Selected School (${state.school.selected}) Not Found"),
        course: String? = null,
        prof: String? = null,
    ) {
        if (!isSameAsState(school.code, dept, course, prof)) return
        pageLoading = true
        coroutineScope.launch {
            // prevent unnecessary network requests & calculations
            val newDept = state.school.selected != school.code || state.dept.selected != dept
            if (newDept) {
                deptData = DeptData(
                    teachingMap = repository.getTeachingData(school.code, dept),
                    allEntriesByProf = repository.getLatestEntriesByProf(school.code, dept),
                )
            }

            val profList = if (newDept)
                entriesByProf.keys.sorted().plusElementAtStart(None)
            else state.prof.list

            val courseList = if (newDept)
                entriesByProf.values.flatMap { it.map(Entry::course) }
                    .toSet().sorted().plusElementAtStart(None)
            else state.course.list

            val courseValid = course in courseList
            val oldState = state
            state = state.copy(
                school = state.school.copy(globalData.schoolMap.values, school.code),
                dept = state.dept.copy(school.associateDeptsToName(), dept),
                course = state.course.copy(
                    courseList,
                    course.takeIf { courseValid } ?: None,
                ),
                prof = state.prof.copy(
                    profList,
                    prof.takeIf { !courseValid && it in profList } ?: None,
                ),
            )

            initialLoading = false
            if (oldState == state) pageLoading = false // must be done manually since screen won't recompose
        }
    }

    fun selectCourse(course: String) {
        state = state.copy(
            course = state.course.copy(selected = course),
            prof = state.prof.copy(selected = None)
        )
    }

    fun selectProf(prof: String) {
        state = state.copy(
            prof = state.prof.copy(selected = prof),
            course = state.course.copy(selected = None)
        )
    }

    val profSummaryVM: ProfSummaryVM? by derivedStateOf {
        if (status != Status.Prof) null
        else {
            ProfSummaryVM(
                entriesByProf = entriesByProf,
                selectedProf = state.prof.selected,
                deptCode = getCode(course = None),
                deptUrl = getUrl(prof = None),
                getCourseUrl = { getUrl(prof = None, course = it) },
                goToDeptData = { selectProf(None) },
                goToCourseData = { selectCourse(it) }
            )
        }
    }

    init {
        coroutineScope.launch {
            globalData = GlobalData(
                schoolMap = repository.getSchoolMap("data-9-by-prof"),
                deptMap = repository.getDeptMap(),
                allInstructors = repository.getAllInstructors()
            )
            searchBarVM = SearchBarVM()
            selectSchool(
                school = urlParams["school"],
                dept = urlParams["dept"],
                course = urlParams["course"],
                prof = urlParams["prof"]?.decodeURLParam()?.uppercase(),
            )
        }
    }

    fun onPopState(paramsUrl: String) {
        val paramsMap = paramsUrl.drop(1).split('&').associate {
            it.split('=', limit = 2).zipWithNext().getOrNull(0) ?: return
        }
        val school = paramsMap["school"] ?: return
        val dept = paramsMap["dept"] ?: return
        val course = paramsMap["course"]
        val prof = paramsMap["prof"]?.decodeURLParam()

        // need to figure out how to deal wih invalid course/prof on pop -maybe?
        if (school != state.school.selected) {
            selectSchool(school = school, dept = dept, course = course, prof = prof)
        } else if (dept != state.dept.selected) {
            selectDept(dept = dept, course = course, prof = prof)
        } else if (course != null) {
            selectCourse(course)
        } else if (prof != null) {
            selectProf(prof)
        } else if (state.course.selected != None) {
            selectCourse(None)
        } else if (state.prof.selected != None) {
            selectProf(None)
        }
    }

    private fun getUrl(
        school: String = state.school.selected,
        dept: String = state.dept.selected,
        course: String = state.course.selected,
        prof: String = state.prof.selected,
    ): String {
        return "?" +
                "school=$school" +
                "&dept=$dept" +
                with(course) { if (isBlankOrNone()) "" else "&course=$this" } +
                with(prof) { if (isBlankOrNone()) "" else "&prof=${encodeURLParam()}" }
    }

    private fun getCode(
        school: String = state.school.selected,
        dept: String = state.dept.selected,
        course: String = state.course.selected,
    ): String {
        return "$school:$dept" + with(course) { if (isBlankOrNone()) "" else ":$this" }
    }

    private fun isSameAsState(school: String, dept: String, course: String?, prof: String?): Boolean {
        return school != state.school.selected ||
                dept != state.dept.selected ||
                (course ?: None) != state.course.selected ||
                (prof ?: None) != state.prof.selected
    }

    private fun School.associateDeptsToName(): List<Pair<String, String>> {
        return depts.associateWith {
            globalData.deptMap[it] ?: throw IllegalStateException("Invalid Dept ($it)")
        }.toList()
    }
}

private fun Map<String, List<Entry>>.toDisplayMap(addAverage: Boolean = true): Map<String, List<String>> {
    return mapValues { (_, entries) ->
        entries.getTotalRatings().subList(0, 10).getAvesAndTotal()
    }.run { // add Average
        if (!addAverage || size == 1) this
        else {
            val stats = this.values.reduce { acc, pair ->
                acc.first.zip(pair.first) { a, b -> a + b } to acc.second + pair.second
            }
            val ave = stats.first.map { it / this.size }
            plus("Average" to Pair(ave, stats.second))
        }
    }.mapValues { (_, nums) ->
        nums.first.map { jsFormatNum(it, minDecDigits = 2, maxDecDigits = 2) } +
                nums.second.toString()
    }
}

private class GlobalData(
    val schoolMap: Map<String, School>,
    val deptMap: Map<String, String>,
    val allInstructors: List<Instructor>,
)

private class DeptData(
    val teachingMap: Map<String, List<String>> = emptyMap(),
    val allEntriesByProf: Map<String, List<Entry>> = emptyMap(),
)

enum class Status {
    InitialLoading, Dept, Course, Prof,
}

data class DropDownState<T>(
    val list: Collection<T> = emptyList(),
    val selected: String = "",
)

data class DataPageState(
    val school: DropDownState<School> = DropDownState(),
    val dept: DropDownState<Pair<String, String>> = DropDownState(),
    val course: DropDownState<String> = DropDownState(),
    val prof: DropDownState<String> = DropDownState(),
)

private fun String.isBlankOrNone(): Boolean = isBlank() || equals(None)

private val urlEncodings = listOf("," to "%2C", " " to "%20", "." to "%2E")
private fun String.encodeURLParam(): String = urlEncodings.fold(this) { acc, (a, b) -> acc.replace(a, b) }
private fun String.decodeURLParam(): String = urlEncodings.fold(this) { acc, (a, b) -> acc.replace(b, a) }