package io.github.dennistsar.sirs_kobweb.components.sections.dataPage

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.Cursor
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.foundation.layout.Spacer
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.graphics.Colors
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.silk.components.icons.fa.FaRotateRight
import io.github.dennistsar.sirs_kobweb.states.MinSemesterState
import org.jetbrains.compose.web.css.cssRem
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.RangeInput
import org.jetbrains.compose.web.dom.Text

@Composable
fun MinSemOption(state: MinSemesterState) {
    Column(
        Modifier
            .backgroundColor(Colors.Black)
            .borderRadius(12.px)
            .padding(1.cssRem),
        horizontalAlignment = Alignment.CenterHorizontally,
    ) {
        Text("Hide profs with no data after")
        Row(
            Modifier
                .fillMaxWidth()
                .columnGap(0.5.cssRem),
            verticalAlignment = Alignment.CenterVertically
        ) {
            Spacer()
            RangeInput(
                value = state.rangeValue,
                min = state.bounds.first,
                max = state.bounds.second,
            ) {
                onChange { state.setValue(it.value) }
                onInput { state.setRangeValue(it.value) }
            }
            if (state.showResetButton) {
                FaRotateRight(
                    Modifier
                        .flex(1)
                        .cursor(Cursor.Pointer)
                        .onClick { state.reset() }
                )
            } else Spacer() // to keep slider centered
        }
        Text(state.text)
    }
}